<template>
  <div class="modal">
    <div class="modal-container">
      <a :href="data.link" v-if="data" target="_blank" class="modal-title__link">
        <div class="modal-title" v-html="data.title"></div>
      </a>
      <div class="modal-close-button" @click="$emit('closeModal')"></div>
      <transition name="list" mode="out-in">
        <div class="modal-content" v-if="data.value" key="content">
          <div class="modal-items" v-for="i in data.value" :key="i">
            <img :src="i" :alt="i" />
          </div>
        </div>
        <div v-else class="preloader" key="preloader"><preloader /></div>
      </transition>
    </div>
  </div>
</template>

<script>
import Preloader from "@/components/preloader";
export default {
  name: "Modal",
  components: {Preloader},
  props: {
    data: {
      type: Object,
      default: () => {return {}}
    }
  }
}
</script>

<style scoped>
.preloader {
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
}
</style>
<style>
.modal {
  background-color: #fff;
  z-index: 100;
  position: absolute;
  left: 0;
  top: 0;
  right: 0;
  bottom: 0;
  transition: 1s, visibility 0s 1s;
}
.modal-container {
  position: relative;
  max-width: 1200px;
  margin: auto;
  top: 0;
  bottom: 0;
  height: 100vh;
}
.modal-container .modal-title__link {
  text-decoration: none;
  transition: color .1s;
}
.modal-container .modal-title__link:link,
.modal-container .modal-title__link:visited,
.modal-container .modal-title__link:hover,
.modal-container .modal-title__link:focus {
  color: #333;
}
.modal-container .modal-title__link:active {
  color: #ffbf00
}
.modal-container .modal-title {
  position: absolute;
  left: 0;
  top: 30px;
  font-size: 13px;
  font-weight: 500;
  text-align: left;
  max-width: 80%;
  padding: 0 10px;
}
.modal-close-button {
  position: absolute;
  right: 32px;
  top: 32px;
  width: 25px;
  height: 25px;
  opacity: 1;
  cursor: pointer;
}
.modal-close-button:before, .modal-close-button:after {
  position: absolute;
  left: 15px;
  content: ' ';
  height: 25px;
  width: 2px;
  background-color: #333;
}
.modal-close-button:before {
  transform: rotate(45deg);
}
.modal-close-button:after {
  transform: rotate(-45deg);
}

.modal-container .modal-content {
  position: absolute;
  top: 80px;
  bottom: 20px;
  overflow-y: auto;
  width: 100%;
  padding-right: 10px;
}
.modal-container .modal-items {
  background-color: #eee;
  margin-bottom: 10px;
}

.modal-container .modal-items img {
  width: 100%;
}

@media screen and (min-width: 1024px) {

  .modal {
    background-color: #ffffffe8;
  }
  .modal-title {
    top: 40px;
    font-size: 20px;
  }
  .modal-container .modal-content {
    padding-right: 10px;
  }
  .modal-container .modal-content {
    top: 100px;
  }

  /* скроллбар */
  .modal-container .modal-content::-webkit-scrollbar {
    width: 8px; /* ширина для вертикального скролла */
    height: 8px; /* высота для горизонтального скролла */
    background-color: #999;
    border-radius: 4px;
  }

  /* ползунок скроллбара */
  .modal-container .modal-content::-webkit-scrollbar-thumb {
    background-color: #ffbf00;
    border-radius: 4px;
    box-shadow: inset 1px 1px 10px #f6d847;
  }

  .modal-container .modal-content::-webkit-scrollbar-thumb:hover {
    background-color: #ffbf00;
  }

  /* Стрелки */
  .modal-container .modal-content::-webkit-scrollbar-button:vertical:start:decrement,
  .modal-container .modal-content::-webkit-scrollbar-button:vertical:end:increment,
  .modal-container .modal-content::-webkit-scrollbar-button:horizontal:start:decrement,
  .modal-container .modal-content::-webkit-scrollbar-button:horizontal:end:increment {
    display: none;
  }

  .list-enter-active, .list-leave-active {
    transition: all 1s;
  }
  .list-enter, .list-leave-to /* .list-leave-active до версии 2.1.8 */ {
    opacity: 0;
  }
}
</style>