<template>
  <div class="content">
    <!-- Top bar -->
    <top-bar :header="pageData.header" />
    <!-- end Top bar -->
    <slider :slider="slider" :filters-list="filtersList" @filterUpdate="filterUpdate" @inputSlide="transitData"/>
  </div>
</template>

<script>
import TopBar from "@/components/topbar";
import Slider from "@/components/slider";
export default {
  name: "portfolio",
  components: {Slider, TopBar},
  props: {
    slider: {
      type: Array
    },
    filtersList: {
      type: Array
    }
  },
  data() {
    return {
      pageData: {
        header: "Портфолио",
      }
    }
  },
  methods: {
    transitData(data) {
      this.$emit('inputSlide', data)
    },
    filterUpdate(filter) {
      this.$emit('filterUpdate', filter)
    },
  }
}
</script>

<style>
/* clearfix */
.owl-carousel .owl-wrapper:after {
  content: ".";
  display: block;
  clear: both;
  visibility: hidden;
  line-height: 0;
  height: 0;
}
/* display none until init */
.owl-carousel {
  display: none;
  position: relative;
  width: 100%;
  padding:48px 0 0;
  -ms-touch-action: pan-y;
}
.owl-carousel .testim_slider {padding-top:30px;}
.owl-carousel .owl-wrapper{
  display: none;
  position: relative;
  -webkit-transform: translate3d(0px, 0px, 0px);
}
.owl-carousel .owl-wrapper-outer{
  overflow: hidden;
  position: relative;
  width: 100%;
}
.owl-carousel .owl-wrapper-outer.autoHeight{
  transition: height 500ms ease-in-out;
}

.owl-carousel .owl-item{
  float: left;
}
.owl-controls .owl-page,
.owl-controls .owl-buttons div{
  cursor: pointer;
}
.owl-controls {
  user-select: none;
  -webkit-tap-highlight-color: rgba(0, 0, 0, 0);
}

/* mouse grab icon */
.grabbing {
  cursor: pointer;

}

/* fix */
.owl-carousel  .owl-wrapper,
.owl-carousel  .owl-item{
  backface-visibility: hidden;
  transform: translate3d(0,0,0);
}
</style>