<template>
  <header :class="['box-header', isMenuOpen ? 'menu-is-open' : '']">
    <div class="header-container">
      <div class="box-logo">
        <div class="logo__container">
          <img class="logo__img" src="@/assets/face.jpg" alt="logo">
        </div>
      </div>
      <!-- box-nav -->
      <div class="box-primary-nav-trigger" @click="menuClicked()">
        <span class="box-menu-text">Меню</span>
        <span :class="['box-menu-icon', isMenuOpen ? 'is-clicked' : '']"></span>
      </div>
      <!-- box-primary-nav-trigger -->
    </div>
  </header>
</template>

<script>
export default {
  name: 'Header',
  props: {
    isMenuOpen: {
      type: Boolean,
      default: false
    }
  },
  methods: {
    menuClicked() {
      this.$emit('isOpenMenu')
    }
  }
}
</script>

<style>
.box-logo {
  display: block;
  padding: 10px 20px;
  position: relative;
}
 .logo__container {
   max-width: 50px;
   max-height: 50px;
   overflow: hidden;
   border-radius: 50%;
 }
 .logo__img {
   max-width: 100%;
 }
.header-container {
  max-width: 1200px;
  margin: auto;
  position: relative;
}
.box-header {
  position: absolute;
  top: 0;
  left: 0;
  background: rgba(255, 255, 255, 0.95);
  height: 50px;
  width: 100%;
  z-index: 3;
  box-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

.box-header {
  height: 70px;
  background: transparent;
  box-shadow: none;
}

.box-header {
  transition: background-color 0.3s;
  transform: translate3d(0, 0, 0);
  backface-visibility: hidden;
}

.box-header.is-fixed {
  position: fixed;
  top: -80px;
  background-color: rgba(255, 255, 255, 0.96);
  transition: transform 0.3s, -webkit-transform 0.3s;
}

.box-header.is-visible {
  -webkit-transform: translate3d(0, 100%, 0);
  transform: translate3d(0, 100%, 0);
}

.box-header.menu-is-open {
  background-color: rgba(255, 255, 255, 0.96);
}

.box-primary-nav-trigger {
  position: absolute;
  right: 0;
  top: 0;
  height: 100%;
  width: 50px;
  background-color: #fff;
}

.box-primary-nav-trigger .box-menu-text {
  color: #393939;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 14px;
  display: none;
}

.box-primary-nav-trigger .box-menu-icon {
  display: inline-block;
  position: absolute;
  left: 50%;
  top: 50%;
  bottom: auto;
  right: auto;
  transform: translateX(-50%) translateY(-50%);
  width: 18px;
  height: 2px;
  background-color: #393939;
  transition: background-color 0.3s;
  list-style: none;
}
.box-primary-nav-trigger .box-menu-icon::before,
.box-primary-nav-trigger .box-menu-icon:after {
  content: '';
  width: 100%;
  height: 100%;
  position: absolute;
  background-color: #393939;
  right: 0;
  transition: transform .3s, top .3s, background-color 0s, -webkit-transform .3s;
}

.box-primary-nav-trigger .box-menu-icon::before {
  top: -5px;
}

.box-primary-nav-trigger .box-menu-icon::after {
  top: 5px;
}

.box-primary-nav-trigger .box-menu-icon.is-clicked {
  background-color: rgba(255, 255, 255, 0);
}

.box-primary-nav-trigger .box-menu-icon.is-clicked::before,
.box-primary-nav-trigger .box-menu-icon.is-clicked::after {
  background-color: #393939;
}

.box-primary-nav-trigger .box-menu-icon.is-clicked::before {
  top: 0;
  transform: rotate(135deg);
}

.box-primary-nav-trigger .box-menu-icon.is-clicked::after {
  top: 0;
  transform: rotate(225deg);
}

.box-primary-nav-trigger {
  display: flex;
  align-items: center;
  width: 100px;
  padding-left: 1em;
  background-color: transparent;
  height: 100%;
  line-height: 30px;
  right: 10px;
  top: 50%;
  bottom: auto;
  -webkit-transform: translateY(-50%);
  transform: translateY(-50%);
}
.box-primary-nav-trigger:hover {
  cursor: pointer;
}

.box-primary-nav-trigger .box-menu-text {
  display: flex;
}

.box-primary-nav-trigger .box-menu-icon {
  left: auto;
  right: 1em;
  transform: translateX(0) translateY(-50%);
}
</style>