<template>
  <div class="categories-grid wow fadeInLeft">
    <nav class="categories text-center">
      <ul class="portfolio_filter">
        <li
            v-for="filter in filtersList"
            :key="filter.title"
            @click="$emit('filterUpdate', filter)"
        >
          <span :class="filter.value ? 'active' : ''">
            {{ filter.title }}
          </span>
        </li>
      </ul>
    </nav>
  </div>
</template>

<script>
export default {
  name: "Filters",
  props: {
    filtersList: {
      type: Array,
      default: () => {return []}
    }
  }
}
</script>
<style>
@media screen and (min-width: 1024px) {
  .categories-grid .categories ul li {
    list-style: none;
  }

  .categories-grid .categories ul li span {
    display: inline-block;
    color: #60606e;
    padding: 0 10px;
    margin: 0 10px;
    -webkit-transition: all .2s ease-in-out .2s;
    transition: all .2s ease-in-out .2s;
  }

  .categories-grid .categories ul li span:hover,
  .categories-grid .categories ul li span:focus {
    text-decoration: none;
    cursor: pointer;
  }

  .categories-grid .categories ul li span.active {
    margin-left: 0;
    background-color: #ffbf00;
    padding: 0px 20px;
    color: white;
    border-radius: 25px;
    text-decoration: none;
  }

  .portfolio_filter {
    padding-left: 0;
  }

  .categories-grid {
    display: flex;
    align-items: center;
    margin: auto;
  }

  .categories-grid .categories ul li {
    float: left;
  }

  .categories-grid .categories ul li span {
    padding: 0 10px;
    -webkit-transition: all .2s ease-in-out .2s;
    transition: all .2s ease-in-out .2s;
  }

  .portfolio_filter {
    padding-left: 0;
    display: inline-block;
    margin: 0 auto;
    text-align: center;
  }
}
</style>