<template>
  <div @wheel.prevent="wheel" class="container-fluid">
    <transition name="component-fade" mode="out-in" >
      <keep-alive>
        <component :is="view.component" v-bind="view.theProps" @filterUpdate="filterUpdate" @inputSlide="openSlide"></component>
      </keep-alive>
    </transition>
    <div class="mouse">
      <div class="scroll"></div>
    </div>
  </div>
</template>

<script>
// @ is an alias to /src
import Main from '@/components/lending/main.vue'
import About from '@/components/lending/about.vue'
import Services from '@/components/lending/services.vue'
import Portfolio from '@/components/lending/portfolio.vue'

export default {
  name: 'Home',
  data() {
    return {
      count: 0,
      view: {
        component: "Main",
        theProps: {},
      },
      selectedFilter: {
        title: "All",
        value: true
      },
      allViews: [
        {
          component: "Main",
          theProps: {},
        },
        {
          component: "About",
          theProps: {}
        },
        {
          component: "Services",
          theProps: {}
        },
        {
          component: "Portfolio",
          theProps: {
            filtersList: [],
            slider: []
          }
        }
      ],
      filtersList: [
        {
          title: "All",
          value: true
        },
        {
          title: "Web design",
          value: false
        },
        {
          title: "Logo",
          value: false
        },
        {
          title: "Design",
          value: false
        },
        {
          title: "Advertising",
          value: false
        },

      ],
      slider: [
        {
          header: "LingoBros Private French Lesoon`s Website Design",
          tag: ["Web design"],
          src: "/slider/LingoBros_Private_French_Lesoon_s_Website_Design.png",
          alias: "first"
        },
        {
          header: "Eco Shop Social Media Design",
          tag: ["Design", "Advertising"],
          src: "/slider/Eco_Shop_Social_Media_Design.png",
          alias: "2"
        },
        {
          header: "Beeline Mobile Operator",
          tag: ["tag"],
          src: "/slider/Beeline_Mobile_Operator.jpg",
          alias: "3"
        },
        {
          header: "K Service Logo Design",
          tag: ["Logo"],
          src: "/slider/K_Service_Logo_Design.jpg",
          alias: "4"
        },
        {
          header: "Airsoft Event Poster",
          tag: ["Advertising"],
          src: "/slider/Airsoft_Event_Poster.jpg",
          alias: "5"
        },
        {
          header: "Cotti Family Tea",
          tag: ["Design", "Advertising"],
          src: "/slider/Cotti_Family_Tea.png",
          alias: "6"
        },
        {
          header: "Hand Drawn Illustartions for Marry Me dress shop",
          tag: ["Advertising", "Design"],
          src: "/slider/Hand_Drawn_Illustartions_for_Marry_Me_dress_shop.jpg",
          alias: "7"
        },
        {
          header: "HELTER SKELTER Foods",
          tag: ["Advertising"],
          src: "/slider/HELTER_SKELTER_Foods.jpg",
          alias: "8"
        },
        {
          header: "Nihilist",
          tag: ["Advertising"],
          src: "/slider/Nihilist.png",
          alias: "9"
        },
      ]
    }
  },
  components: {
    Main, About, Services, Portfolio
  },
  computed: {
    filteredSlider() {
      if (this.selectedFilter.title === 'All') {
        return this.slider
      } else {
        return this.slider.filter(item => item.tag[0] === this.selectedFilter.title)
      }
    }
  },
  methods: {
    openSlide(data) {
      this.$emit('openSlide', data)
    },
    updateSlider() {
      if(this.view.component==="Portfolio") {
        this.view.theProps = {
          slider: this.filteredSlider,
          filtersList: this.filtersList
        }
      }
    },
    filterUpdate(filter) {
      this.filtersList.find(el => el.title === this.selectedFilter.title).value = false
      this.filtersList.find(el => el.title === filter.title).value = true

      // this.allViews.find(item => item.component==="Portfolio").theProps.filtersList.forEach(el=> {el.value = false})
      // this.allViews.find(item => item.component==="Portfolio").theProps.filtersList.find(el => el.title === filter.title).value=true
      this.selectedFilter = filter
      this.updateSlider()
    },
    wheel: function(ev) {
      if (ev.deltaY < 0) {
        this.count--
        if (this.count < 0) {
          this.count = 0
        }
        this.view = this.allViews[this.count]
      } else {
        if (this.count === this.allViews.length-1) {
          this.count = 0
        } else {
          this.count++
        }
        this.view = this.allViews[this.count]
        this.updateSlider()
      }
    }
  }
}
</script>
<style>
.container-fluid {
  overflow: hidden;
}
.component-fade-enter-active, .component-fade-leave-active {
  transition: opacity .5s ease;
}
.component-fade-enter, .component-fade-leave-to {
  opacity: 0;
}

/* mouse effect */

.mouse {
  position: absolute;
  width: 22px;
  height: 42px;
  bottom: 40px;
  left: 50%;
  margin-left: -12px;
  border-radius: 15px;
  border: 2px solid #888;
  -webkit-animation: intro 1s;
  animation: intro 1s;
}

.scroll {
  display: block;
  width: 3px;
  height: 3px;
  margin: 6px auto;
  border-radius: 4px;
  background: #888;
  -webkit-animation: finger 2s infinite;
  animation: finger 2s infinite;
}

@-webkit-keyframes intro {
  0% {
    opacity: 0;
    -webkit-transform: translateY(40px);
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@keyframes intro {
  0% {
    opacity: 0;
    -webkit-transform: translateY(40px);
    transform: translateY(40px);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateY(0);
    transform: translateY(0);
  }
}

@-webkit-keyframes finger {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(20px);
    transform: translateY(20px);
  }
}

@keyframes finger {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
    -webkit-transform: translateY(20px);
    transform: translateY(20px);
  }
}
.content {
  height: 100vh;
  width: 100%;
  overflow: hidden;
}
.data-container {
  display: flex;
  flex-direction: column;
  max-width: 80%;
  margin: 20px auto auto;
}
.top-bar h1 {
  font-size: 30px;
  text-transform: uppercase;
  font-weight: 700;
  color: #999;
  line-height: 50px;
}
@media screen and (min-width: 1024px) {
  .data-container {
    flex-direction: row;
    max-width: 1200px;
  }
  .top-bar h1 {
    font-size: 60px;
  }
}

</style>