<template>
  <section class="box-intro">
    <div class="table-cell">
      <h1 class="box-headline letters rotate-2">
        <span class="box-words-wrapper">
            <b v-for="(word, numOfWord) in header" :key="`the-word-${word}`"
               :class="numOfWord === nextWord ? 'is-visible' : ''"

            >
              <i v-for="(letter,index) in word" :key="`the-letter-${letter}${index}-in-word-${word}`"
                 :class="rotateLetters(numOfWord)"><em>{{ letter }}</em></i>
            </b>
        </span>
      </h1>
      <h5>Ваш поиск кончается здесь</h5>
    </div>
  </section>
</template>

<script>
export default {
  name: "Main",
  data() {
    return {
      header: [
        "профессионально!",
        "эффективно!",
        "красиво!"
      ],
      nextWord: 0
    }
  },
  methods: {
    rotateWords() {
      setTimeout(() => {
        this.nextWord++
        if (this.nextWord > 2) this.nextWord = 0
        this.rotateWords()
      }, 3500)
    },
    rotateLetters(numOfWord) {
      return numOfWord === this.nextWord ? 'in' : 'out'
    }
  },
  mounted() {
    this.rotateWords()
  }
}
</script>

<style>
.box-intro {
  text-align: center;
  display: table;
  height: 100vh;
  width: 100%;
  overflow: hidden;
}
.box-intro .table-cell {
  display: table-cell;
  vertical-align: middle;
}

.box-intro em {
  font-style: normal;
  text-transform: uppercase
}

b i:last-child {
  color: #ffbf00 !important;
}

.box-intro h5 {
  letter-spacing: 4px;
  text-transform: uppercase;
  color: #999;
  line-height: 1.7
}

/* text rotate */

.box-headline {
  font-size: 100px;
  line-height: 100%;
}

.box-words-wrapper {
  display: inline-block;
  position: relative;
  width: 100%;
  text-align: center;
}

.box-words-wrapper b {
  display: inline-block;
  position: absolute;
  white-space: nowrap;
  left: 0;
  right: 0;
  top: 0;
}

.box-words-wrapper b.is-visible {
  position: relative;
}

.no-js .box-words-wrapper b {
  opacity: 0;
}

.box-words-wrapper b.is-visible {
  opacity: 1;
}

.box-headline.rotate-2 .box-words-wrapper {
  perspective: 300px;
}

.box-headline.rotate-2 i,
.box-headline.rotate-2 em {
  display: inline-block;
  backface-visibility: hidden;
}

.box-headline.rotate-2 i {
  transform-style: preserve-3d;
  transform: translateZ(-20px) rotateX(90deg);
  opacity: 0;
}

.is-visible .box-headline.rotate-2 i {
  opacity: 1;
}

.box-headline.rotate-2 i.in {
  animation: box-rotate-2-in 0.4s forwards;
}

.box-headline.rotate-2 i.out {
  animation: box-rotate-2-out 0.4s forwards;
}

.box-headline.rotate-2 em {
  transform: translateZ(20px);
}

.no-csstransitions .box-headline.rotate-2 i {
  transform: rotateX(0deg);
  opacity: 0;
}

.no-csstransitions .box-headline.rotate-2 i em {
  transform: scale(1);
}

.no-csstransitions .box-headline.rotate-2 .is-visible i {
  opacity: 1;
}

@-webkit-keyframes box-rotate-2-in {
  0% {
    opacity: 0;
    -webkit-transform: translateZ(-20px) rotateX(90deg);
  }
  60% {
    opacity: 1;
    -webkit-transform: translateZ(-20px) rotateX(-10deg);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateZ(-20px) rotateX(0deg);
  }
}

@keyframes box-rotate-2-in {
  0% {
    opacity: 0;
    -webkit-transform: translateZ(-20px) rotateX(90deg);
    transform: translateZ(-20px) rotateX(90deg);
  }
  60% {
    opacity: 1;
    -webkit-transform: translateZ(-20px) rotateX(-10deg);
    transform: translateZ(-20px) rotateX(-10deg);
  }
  100% {
    opacity: 1;
    -webkit-transform: translateZ(-20px) rotateX(0deg);
    transform: translateZ(-20px) rotateX(0deg);
  }
}

@-webkit-keyframes box-rotate-2-out {
  0% {
    opacity: 1;
    -webkit-transform: translateZ(-20px) rotateX(0);
  }
  60% {
    opacity: 0;
    -webkit-transform: translateZ(-20px) rotateX(-100deg);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateZ(-20px) rotateX(-90deg);
  }
}

@keyframes box-rotate-2-out {
  0% {
    opacity: 1;
    -webkit-transform: translateZ(-20px) rotateX(0);
    transform: translateZ(-20px) rotateX(0);
  }
  60% {
    opacity: 0;
    -webkit-transform: translateZ(-20px) rotateX(-100deg);
    transform: translateZ(-20px) rotateX(-100deg);
  }
  100% {
    opacity: 0;
    -webkit-transform: translateZ(-20px) rotateX(-90deg);
    transform: translateZ(-20px) rotateX(-90deg);
  }
}
@media screen and (max-width: 1270px) {
  .box-headline {
    font-size: 50px;
    transition: font-size .3s ease-in-out;
  }
}
@media screen and (max-width: 670px) {
  .box-headline {
    font-size: 40px;
    transition: font-size .3s ease-in-out;
  }
}
@media screen and (max-width: 540px) {
  .box-headline {
    font-size: 20px;
    transition: font-size .3s ease-in-out;
  }
  h5 {
    font-size: 10px;
  }
}
</style>