              <em>{{slideData.tag.join('/')}}</em>
            </div>
          </div>
        </div>
      </div>
    </div>
</template>

<script>
export default {
  name: "Slide",
  props: {
    slideData: {
      type: Object,
    }
  },
}
</script>
