<template>
  <section class="slider">
    <div class="slider-container">
      <div class="slider__header">
        <div class="_header">
          <!-- portfolio_filter -->
          <filters :filters-list="filtersList" @filterUpdate="filterUpdate" />
          <!-- portfolio_filter -->
        </div>
        <div class="slider__controls">
          <div class="previous" @click="increase()"><i class="left__arrow"></i></div>
          <div class="next" @click="decrease()"><i class="right__arrow"></i></div>
        </div>
      </div>

      <div class="slider-carousel">
        <transition-group
            tag="div"
            name="moving-list"
            mode="out-in"
            :style="{left: `${offsetX}px`}"
            class="slider__line"
        >
          <slide :style="{left: `${index*320}px`}" class="moving-list-item" v-for="(slide, index) in slider" :key="slide.alias" :slide-data="slide" @inputSlide="transitData"/>
        </transition-group>
      </div>
    </div>
  </section>
</template>

<script>
import Slide from "@/components/slide";
import Filters from "@/components/filters";

export default {
  name: "Slider",
  components: {Filters, Slide},
  props: {
    slider: {
      type: Array
    },
    filtersList: {
      type: Array
    }
  },
  data() {
    return {
      offsetX: 0
    }
  },
  methods: {
    screen() {
      return window.innerWidth
    },
    transitData(data) {
      this.$emit('inputSlide', data)
    },
    filterUpdate(filter) {
      this.offsetX = 0
      this.$emit('filterUpdate', filter)
    },
    increase() {
      if (this.screen() < this.slider.length * 320) {
        if (this.screen() > 1024) {
          this.offsetX += 320
          if (this.offsetX > 0) {
            this.offsetX = this.screen() - this.slider.length * 320
          }
        } else {
          this.offsetX += this.screen()
          if (this.offsetX > 0) {
            this.offsetX = -this.slider.length * this.screen()
          }
        }
      }
    },
    decrease() {
      if (this.screen() > 1024) {
        this.offsetX -= 320
        if (this.offsetX < this.screen() - this.slider.length * 320) {
          this.offsetX = 0
        }
      } else {
        this.offsetX -= this.screen()
        if (this.offsetX < - this.slider.length * this.screen()) {
          this.offsetX = 0
        }
      }
    }
  }
}
</script>

<style scoped>
.slider {
  margin-top: 60px;
  height: 55vh;
  overflow: hidden;
}

.slider__header {
  display: flex;
}

.slider__header ._header {
  width: 73.3333%;
}

.slider__header .slider__controls {
  height: 100%;
}

.slider__controls {
  display: flex;
  cursor: pointer;
}

.slider__controls .previous,
.slider__controls .next {
  position: relative;
  width: 50px;
  height: 50px;
  margin: 0 0 0 -2px;
}

.slider__controls .previous:before, .slider__controls .next:before {
  content: "";
  width: 100%;
  height: 100%;
  box-sizing: border-box;
  border: 2px solid #999;
  position: absolute;
  transition: all .3s;
  left: 0;
  top: 0;
}

.previous .left__arrow:before,
.next .right__arrow:before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;

  transform: translate(-50%, -50%);
  transition: all .3s ease-in-out;
}

.previous .left__arrow:before {
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;

  border-right: 10px solid #999;

}

.next .right__arrow:before {
  border-top: 10px solid transparent;
  border-bottom: 10px solid transparent;

  border-left: 10px solid #999;
}

.slider-carousel {
  height: 45vh;
  width: 100%;
  overflow: hidden;
}
.slider__line {
  position: relative;
  left: 0;
  height: 100%;
  display: flex;
  transition: left .3s ease-out;
}

.moving-list-item {
  transition: all .3s;
  position: absolute;
}
.moving-list-enter, .moving-list-leave-to {
  opacity: 0;
  transform: scale(0);
}
.moving-list-leave-active {
  transition: all .3s ease-in-out;
}

@media screen and (max-width: 600px) {

}

@media screen and (min-width: 600px) {

}

@media screen and (min-width: 1024px) {
  .slider {
    height: 35vh;
  }

  .slider__header {
    margin-bottom: 20px;
  }
  .slider ._header {
    display: flex;
  }
  .slider-carousel {
    height: 250px;
  }

  .previous:hover .left__arrow:before,
  .next:hover .right__arrow:before {
    border-left-color: #ffbf00;
    border-right-color: #ffbf00;
  }
}

</style>